.header {
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 230px;
    max-width: 1600px;
    margin: 0 auto;
  
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-image: url('/src/images/background-with-red-lines_mob.jpg');
  
    @media (min-device-pixel-ratio: 2),
      (-webkit-min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url('/src/images/background-with-red-lines_mob_2x.jpg');
    }
  
    @media (min-width: 768px) {
      height: 216px;
      background-image: url('/src/images/background-with-red-lines_tab.jpg');
  
      @media (min-device-pixel-ratio: 2),
        (-webkit-min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
        background-image: url('/src/images/background-with-red-lines_tab_2x.jpg');
      }
    }
  
    @media (min-width: 1280px) {
      background-image: url('/src/images/background-with-red-lines_desk.jpg');
  
      @media (min-device-pixel-ratio: 2),
        (-webkit-min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
        background-image: url('/src/images/background-with-red-lines_desk_x2.jpg');
      }
    }
  }
  
  .site-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .site-nav__logo {
    display: flex;
    align-items: center;
    padding-top: 40px;
    padding-bottom: 40px;
    padding-right: 6px;
  }
  
  .logo-text {
    display: none;
    margin-left: 8px;
    color: #fff;
    font-style: 'Roboto';
    font-weight: 500;
    font-size: 30px;
    line-height: 1.17;
  
    @media (min-width: 768px) {
      display: block;
    }
  }
  
  .menu {
    display: flex;
    text-transform: uppercase;
    color: #fff;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.17;
  }
  
  .menu__item + .menu__item {
    margin-left: 40px;
  }
  
  .menu__link {
    display: block;
    padding-bottom: 3px;
    padding-top: 5px;
    height: 100%;
  }
  
  .menu-link--current {
    position: relative;
  
    &::after {
      position: absolute;
      bottom: 0;
      right: 0;
      content: '';
      background-color: #ff001b;
      width: 100%;
      height: 3px;
    }
  }
  
  .search-form {
    display: flex;
    justify-content: center;
    margin-top: 14px;
  
    @media (min-width: 768px) {
      margin-top: 0px;
    }
  }
  
  .search-form__input {
    position: relative;
    width: 100%;
  
    @media (min-width: 768px) {
      width: 336px;
    }
  
    @media (min-width: 1280px) {
      width: 394px;
    }
  }
  
  .search-form__input-field {
    width: 100%;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.17;
    color: #fff;
  
    background-color: transparent;
    border-top: none;
    border-left: none;
    border-right: none;
    border-bottom: 0.5px solid white;
    opacity: 1;
  
    &:focus {
      outline: 0;
      outline-offset: 0;
      border-radius: 4px;
      cursor: pointer;
      color: #fff;
    }
    &::placeholder {
      color: #fff;
      opacity: 1;
    }
  
    &:focus::placeholder {
      color: #FFFFFF;
      opacity: 0;
      transition: color 1.5s ease;
      transition: opacity 1.5s ease;
    }
  }
  .search-form__input-field:active {
    border-bottom: #FFFFFF;
  }
  
  .button-search {
    position: absolute;
    background-color: inherit;
    border: none;
    padding-right: 0px;
    padding-left: 10px;
    right: 0px;
    top: 0px;
  }
  
  .label-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
  
  .error-search {
    display: flex;
    justify-content: center;
    // visibility: hidden;
    margin-top: 16px;
    font-weight: 400;
    font-size: 14;
    color: #ff001b;
    line-height: 1.17;
  
    @media (min-width: 768px) {
      margin-top: 20px;
    }
  
    @media (min-width: 1280px) {
      margin-top: 20px;
    }
  }
  
  .visible {
    visibility: visible;
  }
  
  .is-hidden {
    display: none;
  }
