.container {
  @media screen and (max-width:767px) {
    max-width: 100%;
    padding-left: 20px;
    padding-right: 20px;
    margin: 0 auto;
    max-width: 320px;
      .main-list{
      padding-top: 20px;
      padding-bottom: 20px;

      .table-item:not(:last-child) {
        margin-bottom: 20px;
      }
   }
  }  


    @media screen and (min-width: 768px) and (max-width:1279px)  {
      .main-list {
        display: flex;
      flex-wrap: wrap;
      gap: 32px;
      padding-top: 60px;
      padding-bottom: 60px;
      
      }

      padding-left: 32px;
      padding-right: 32px;
      margin-left: auto;
      margin-right: auto;
      width: 768px;
      
}

  @media (min-width: 1280px) {
      width: 1280px;
      margin-right: auto;
      margin-left: auto;
      padding-left: 32px;
      padding-right: 32px;
      

      .main-list {
        display: flex;
        flex-wrap: wrap;
        max-width: 1216px;
        padding-top: 60px;
        padding-bottom: 60px;
      }
      .table-item {
        margin-right: 16px;
        margin-bottom: 32px;
      }

      .table-item:nth-child(3n) {
        margin-right: 0;
      }


      .table-item:last-child {
        margin-right: 0;
      }
      
    }
  }
  


  


  .table-item {
    width: 280px;
    @media screen and (min-width:768px) {
      width: 336px;
    }
    @media screen and (min-width:1280px) {
      width: 394px;

    }
  }