@import '~node_modules/modern-normalize/modern-normalize.css';


  
img {
    display: block;
    max-width: 100%;
    height: auto;
  }
  
  button {
    cursor: pointer;
  }
  
  h2,h3,h4,p{
    margin: 0;
    padding: 0;
  }
  
  ul,li{
    padding: 0;
    margin: 0;
    list-style: none;
  }

  a{
    text-decoration: none;
    color: inherit;
  }